.hero-container {
    margin: 16px;
    margin-top: 32px;
    .hero-section {
        display: inline-flex;
        .hero-image {
            width: 150px;
            height: 100px;
            border-radius: 10%;
        }
        .hero-name {
            margin: 10px;
            margin: auto 16px;
        }
    }
    .hero-body {
        display: inline-flex;
        margin-top: 32px;
    }
}